@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@300&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Montserrat:wght@500&display=swap');

:root {
  --primary: #148056;
  --lightGray: rgb(129, 126, 126);
  --darkBlack: black;
  --lightBlack: rgba(26, 22, 22, 0.881);
  --bgWhite: #eaf0f5;
  --white: #fff;
  --secondaryText: rgb(220, 221, 222);
}


* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: 'Poppins', sans-serif;
  scroll-behavior: smooth;
}

html {
  width: 100vw;
  overflow-x: hidden;
  background-color: var(--bgWhite);
}

.font-headers {
  font-family: 'Montserrat', sans-serif;
}


li {
  list-style: none;
}

a {
  text-decoration: none;
}


.flex-col {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}


.about-wrapper {
  background-color: var(--primary);
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-wrap: wrap;
  width: 100%;
  padding: 20px;
}

#aboutus,
#contactus {
  flex: 1 0 250px;
  color: var(--secondaryText);
}



#aboutus .wrapper {
  width: 95%;
}

#aboutus .header h1,
#contactus .header h1 {
  font-size: 23px;
  text-align: left;
  color: var(--white);
}

.address {
  width: 100%;
  margin-top: 15px;
}

.icons {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  font-size: 27px;
  margin-top: 15px;
  /* color: var(--lightBlack); */
  max-width: max-content;

}


.des {
  font-size: 17px;
  width: 100%;
  padding-right: 20px;
}

.icons {
  font-size: 16px;
  font-weight: bold;
  color: var(--secondaryText);
  transition: all .3s ease-in-out;
}

.singleIcon {
  font-size: 22px;
}

.icons:hover {
  color: var(--darkBlack);
}

.icons p {
  margin-left: 10px;
}


.social {
  margin-top: 15px;
}

.social h5 {
  font-size: 20px;
  font-weight: bold;
  color: var(--white);
}

.social-icons {
  width: 100%;
}

.social-icons a {
  color: var(--secondaryText);
  margin-left: 2rem;
  font-size: 28px;
  transition: all .3s ease-in-out;
}

.facebook:hover {
  color: rgb(7, 29, 150);
  animation: rotation .4s linear;
}

.instagram:hover {
  color: rgb(180, 4, 116);
  animation: rotation .4s linear;

}

.whatsapp:hover {
  color: rgb(2, 58, 13);
  animation: rotation .4s linear;
}

@keyframes rotation {
  from {
    transform: rotate(0deg);
  }

  to {
    transform: rotate(359deg);
  }
}




footer {
  width: 100%;
  text-align: center;
  background-color: var(--primary);
  color: var(--secondaryText);
  border-top: 1px solid gainsboro;
  font-weight: bold;
  padding: 8px;
}



