.loader {
    width: 100vw;
    height: 100vh;
    position: fixed;
    top: 0;
    left: 0;
    background-color: rgba(237, 237, 237, 0.877);
    display: flex;
    justify-content: center;
    align-items: center;
    z-index: 99999;
}

.d-none {
    display: none;
}


.loader .laoderImage {
    width: 50%;
    height: 50%;
    display: flex;
    justify-content: center;
    align-items: center;
}

.loader .laoderImage img {
    height: 140px;
    width: 140px;
    animation: rotateLoading 2s backwards infinite;
}

@keyframes rotateLoading {
    from {
        transform: rotateY(0deg);
    }

    to {
        transform: rotateY(360deg);
    }

    /* 100% {
        transform: rotate(0deg);
    } */
}