.cardWrapper {
    width: 100%;
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    flex-wrap: wrap;
    gap: 2rem;
    padding: 15px 10px;

}

.card {
    width: 20%;
    border-radius: 8px;
    min-width: 220px;
    box-shadow: 0px 0px 5px 0px #7a807f28;
    transition: all .4s ease-in-out;
    color: var(--lightBlack);
    background: var(--white);
}

.card:hover {
    box-shadow: inset 0px 0px 20px 0px #cbcbcb5c;
}

.img {
    width: 100%;
    height: 140px;
    display: flex;
    justify-content: center;
    align-items: center;
}

.img img {
    width: 100%;
    height: 100%;
    border-top-right-radius: 8px;
    border-top-left-radius: 8px;
}

.product-info {
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}

.name {
    margin-top: 2px;
    width: 100%;
    font-size: 17px;
    padding-left: 20px;
}

.formula {
    align-self: flex-start;
    width: max-content;
    font-size: 14px;
    font-weight: bold;
    background-color: rgb(27, 27, 27);
    color: rgb(206, 206, 206);
    margin-left: 20px;
}

.infos {
    width: 100%;
    font-weight: bold;
    font-size: 12px;
    margin-top: 4px;
    padding-left: 20px;

}

.infos span {
    margin-left: 3px;
    font-weight: normal;
}

.btnDiv {
    width: 100%;
    display: flex;
    justify-content: center;
    margin: 8px 0px;
}

.readMore {
    font-weight: bold;
    font-size: 16px;
    padding: 0.5px 15px;
    background: white;
    color: var(--primary);
    border: 1px solid var(--primary);
    cursor: pointer;
    transition: all .3s ease-in-out;
}

.readMore:hover {
    background-color: var(--primary);
    color: white;
}


.item {
    width: 30%;
    min-width: 240px;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-bottom: 30px;
}

.icon {
    font-size: 70px;
    height: 80px;
}

.text {
    margin-left: 10px;
}

.text h5 {
    font-size: 20px;
    font-weight: bolder;
}

.text p {
    width: 90%;
    font-size: 14px;
    font-weight: bold;
}


.singleCat {
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
    border-bottom: 2px solid rgb(241, 241, 241);
    padding: 0px 10px;
    padding-top: 8px;
}

.singleCat h6 {
    text-transform: capitalize;
    font-size: 18px;
    font-weight: 800;
}

.deleteCat {
    font-size: 21px;
    color: var(--primary);
    cursor: pointer;
}

.deleteCat:hover {
    color: red;
}

.singleProd {
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    border-bottom: 2px solid rgb(241, 241, 241);

}

.listImage {
    width: 15%;
    height: fit-content;
    display: flex;
    justify-content: center;
    align-items: center;
}

.listImage img {
    width: 100%;
    max-height: 150px;
}

.infoDiv {
    width: 68%;
    display: flex;
    justify-content: flex-start;
    align-items: flex-start;
    flex-direction: column;
    margin: 2px;
    margin-left: 20px;
}

.name-formula h2 {
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 22px;
    font-weight: 800;
}

.name-formula h2 span {
    background-color: var(--lightBlack);
    color: var(--bgWhite);
    font-size: 14px;
    margin-left: 10px;
}

.composition {
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: flex-start;
    flex-wrap: wrap;
    text-transform: capitalize;
    gap: 11px;
    max-height: 60px;
    overflow-y: hidden;
}

.singleCOmposition p {
    font-size: 14px;
    font-weight: bold;
    color: rgb(71, 71, 71);
    padding-right: 12px;
    min-width: max-content;
}

.options {
    width: 15%;
    min-width: 10px;
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    flex-wrap: wrap;
    gap: 10px;
}

.optionIcons {
    cursor: pointer;
    font-size: 23px;
}

.edit {
    color: var(--primary);
    transition: .3s ease-in-out;
}

.delete {
    color: red;
    transition: all .4s ease-in-out;
}

.optionIcons:hover {
    color: black;
}