.modal {
    position: fixed;
    width: 100vw;
    height: 100vh;
    top: 0%;
    left: 0%;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    z-index: 999;
}

.bg-solid {
    background-color: var(--bgWhite);
}

.bg-trans {
    background-color: rgba(116, 116, 116, 0.63);
}

.modal-closed {
    display: none;
}