header {
    width: 100%;
    padding-left: 25px;
    padding-right: 25px;
    position: fixed;
    top: 0;
    left: 0;
    z-index: 99;
    display: flex;
    justify-content: space-between;
    align-items: center;
    height: 70px;
}

.bg-nav {
    background-color: var(--primary);
}

.list-items {
    color: var(--bgWhite);
    transition: all .3s ease-in-out;

}

.list-items:hover {
    border-bottom: 2px solid var(--bgWhite);
}

.secondary-nav {
    background-color: var(--bgWhite);
}


.primary-nav-list-items {
    color: var(--primary);
    transition: all .3s ease-in-out;
}

.primary-nav-list-items:hover {
    border-bottom: 2px solid var(--primary);
}


.logo {
    display: flex;
    justify-content: flex-start;
    align-items: center;
}

.logo img {
    width: 70px;
    height: 60px;
}

nav a {
    margin: 1.5rem;
    font-size: 15px;
    font-weight: 600;
}

.burger {
    display: none;
}


input#openSidebarMenu {
    display: none;
}

.sidebarIconToggle {
    cursor: pointer;
    margin-right: 10px;
}


.spinner {
    height: 3px;
    width: 23px;
    background-color: var(--darkBlack);
    transition: all 1s;
}

#openSidebarMenu:checked~.sidebarIconToggle>.spinner.middle {
    opacity: 0;
}

#openSidebarMenu:checked~.sidebarIconToggle>.spinner.top {
    transform: rotate(140deg) translate(-15%, 100%);
    margin-top: 9px;
}

#openSidebarMenu:checked~.sidebarIconToggle>.spinner.bottom {
    transform: rotate(-140deg) translate(11%, 150%);
    margin-top: -8px;
}

.spinner.middle,
.spinner.bottom {
    margin-top: 3px;
}

.animateMenu {
    animation: animatemenu 0.8s linear;
}

@keyframes animatemenu {
    0% {
        top: -10%;
        opacity: 0;
    }

    50% {
        top: -5%;
        opacity: 0.5;
    }

    100% {
        opacity: 1;
        top: 0;
    }
}


@media screen and (max-width:850px) {
    nav a {
        margin: 0.7rem;
        font-size: 15px;
    }
}

@media screen and (max-width:715px) {
    nav a {
        margin: 0.5rem;
        font-size: 14px;

    }
}

@media screen and (max-width:650px) {
    header {
        background-color: var(--darkBlack);
    }

    .spinner {
        background-color: var(--primary);
    }

    .burger {
        display: flex;
        justify-content: center;
        align-items: center;
    }

    header nav {
        position: fixed;
        top: 0;
        left: 0;
        width: 100%;
        height: 80%;
        display: flex;
        justify-content: center;
        align-items: center;
        gap: 1.2rem;
        background-color: var(--darkBlack);
        transition: 1s;
        transform: translateY(-100vh);
        flex-direction: column;
    }

    header .responsive_nav {
        transform: none;
    }

}