.product-Wrapper {
    min-height: 60vh;
    width: 100%;
    margin-top: 100px;
    margin-bottom: 100px;
}

.singleProductWrapper {
    margin: auto;
    width: 70%;
    background-color: var(--white);
    display: flex;
    justify-content: center;
    align-items: center;
    flex-wrap: wrap;
    gap: 20px;
}

.imageDiv {
    flex: 1 0 220px;
    width: 40%;
    margin-left: 5px;
}

.imageDiv img {
    width: 100%;
    height: 300px;
}

.product-infos {
    flex: 1 0 450px;
    width: 60%;
    align-self: center;

}

.product-name {
    width: 100%;
    display: flex;
    justify-content: flex-start;
    align-items: center;
}

.product-name h1 {
    font-size: 28px;
    font-weight: bolder;
}

.product-name p {
    margin-left: 10px;
    padding: 0px 8px;
    font-weight: bold;

    background-color: var(--lightGray);
}

.single-item {
    display: flex;
    justify-content: flex-start;
    align-items: center;
    margin-top: 10px;
}

.attrib {
    min-width: 110px;
    font-weight: bold;
}

.single-item span {
    font-weight: bold;
    width: 20px;
    font-size: 16px;
}

.values {
    font-size: 14px;
    font-weight: bold;
    color: var(--lightBlack);
}

@media screen and (max-width:1200px) {
    .wrapper {
        width: 80%;
    }
}

@media screen and (max-width:1050px) {
    .wrapper {
        width: 90%;
    }
}

@media screen and (max-width:480px) {
    .product-infos {
        flex: 1 0 200px;
    }

    .attrib {
        font-size: 14px;
    }

    .values {
        font-size: 12px;
    }
}