.form {
    margin: auto;
    width: 65%;
    display: flex;
    justify-content: space-between;
    align-items: center;
    flex-direction: column;
    background-color: var(--white);
    min-height: 20vh;
    box-shadow: 0px 0px 10px 0px #5c5c5cc3;
}

.header {
    width: 100%;
    padding: 15px 4px;
    text-align: center;
    background-color: var(--primary);
    color: var(--bgWhite);
}

.error {
    width: 100%;
    text-align: center;
}

.error p {
    color: red;
    margin: 10px;
    font-size: 14px;
    font-weight: bold;
}

.childrens {
    width: 100%;
    padding: 15px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}


@media screen and (max-width:890px) {
    .form {
        width: 80%;
    }
}


@media screen and (max-width:730px) {
    .form {
        width: 90%;
    }
}

@media screen and (max-width:450px) {
    .form {
        width: 95%;
    }
}