.adminSection {
    position: relative;
    width: 100vw;
    min-height: 100vh;
    padding: 15px;
}

.emptyproducts {
    font-size: 21px;
    text-align: center;
    padding: 5px;
}

.formSection {
    width: 100vw;
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
}

.buttonWrapper {
    padding-top: 30px;
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 40px;
    width: 100%;
}

.mainWrapper {
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 20px 10px;
    gap: 15px;
    flex-direction: column;
}

.categoryWrapper {
    height: 40px;
    width: 90%;
    height: max-content;
    background-color: var(--white);

}

.headerInfo {
    padding: 10px;
    background-color: var(--primary);
}

.headerInfo h2 {
    font-size: 23px;
    color: var(--bgWhite);
}

.productMainWrapper {
    width: 90%;
    margin-top: 20px;
    /* align-self: flex-start; */
}

.prodWrapper {
    background-color: var(--white);
}

.btnWrapper {
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 2rem;
}


.formsection {
    min-height: 100vh;
    width: 100vw;
    padding: 15px;
    display: flex;
    justify-content: center;
    align-items: center;
}

.formInputWrapper {
    width: 95%;
    display: flex;
    justify-content: space-between;
    align-items: center;
    flex-wrap: wrap;
    gap: 20px;
}

.singleInput {
    width: 45%;
    flex: 1 0 240px;
}