.btn {
    font-weight: bold;
    font-size: 16px;
    padding: 6px 15px;
    background: white;
    color: var(--primary);
    border: 1px solid var(--primary);
    cursor: pointer;
    transition: all .3s ease-in-out;
}

.btn-primary {
    font-weight: bold;
    font-size: 16px;
    padding: 6px 15px;
    background: var(--primary);
    color: var(--bgWhite);
    border: 1px solid var(--primary);
    cursor: pointer;
    transition: all .3s ease-in-out;

}

.btn:hover {
    background-color: var(--primary);
    color: white;
}

.btn-primary:hover {
    background-color: var(--bgWhite);
    color: var(--primary);
}