.banner {
    padding-top: 30px;
    height: 100vh;
    width: 100%;
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    background: linear-gradient(to left, #118a40, #17766e);
}

.custom-shape-divider-bottom-1680355369 {
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100%;
    overflow: hidden;
    line-height: 0;
    transform: rotate(180deg);
}

.custom-shape-divider-bottom-1680355369 svg {
    position: relative;
    display: block;
    width: calc(145% + 1.3px);
    height: 132px;
}

.custom-shape-divider-bottom-1680355369 .shape-fill {
    fill: var(--bgWhite);
}

.text-content {
    color: var(--secondaryText);
    width: 50%;
    margin-bottom: 30px;
}

.text-content h1 {
    font-size: 60px;
}

.text-content h5 {
    font-size: 27px;
    margin-bottom: 15px;
}

.text-content p {
    font-size: 19px;
    width: 90%;
}

.bannerImage {
    width: 40%;
    display: flex;
    justify-content: center;
    align-items: center;
}

.bannerImage img {
    width: 100%;
    height: 450px;
}


/* listing css */
.header-text {
    font-size: 30px;
}

.categories ul {
    margin-top: 10px;
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-bottom: 20px;
}

.category {

    font-size: 17px;
    font-weight: 600;
    margin-left: 2rem;
    margin-right: 2rem;
    cursor: pointer;
    color: var(--lightGray);
    transition: color .4s ease-in-out;
}

.categories ul li:hover {
    color: var(--darkBlack);
}

.categories ul li.active {
    border-bottom: 2px solid var(--primary);
}


#whyus {
    padding-top: 30px;
    background-color: var(--whitesh);
}


.text-center {
    text-align: center;
    margin-bottom: 15px;
}

.item-wrapper {
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    gap: 2rem;
    flex-wrap: wrap;
    padding: 10px 25px;
}

@media screen and (max-width:900px) {
    .bannerImage img {
        height: 300px;
    }
}

@media screen and (max-width:690px) {
    .banner {
        padding-top: 90px;
        padding-bottom: 30px;
        height: max-content;
        display: flex;
        justify-content: space-evenly;
        align-items: center;
        flex-direction: column;
    }

    .text-content {
        width: 90%;
    }


    .text-content h5 {
        font-size: 21px;
        margin-bottom: 15px;
    }

    .text-content p {
        font-size: 14px;
        width: 90%;
    }

    .text-content h1 {
        font-size: 45px;
    }

    .bannerImage img {
        width: 450px;
        height: 45px;
    }

    .custom-shape-divider-bottom-1680355369 {
        display: none;
    }
}

@media screen and (max-width:490px) {
    .bannerImage img {
        width: 350px;
        height: 450px;
    }
}

@media screen and (max-width:360px) {
    .bannerImage img {
        width: 280px;
        height: 380px;
    }
}