.bar-div {
    margin-top: 30px;
    text-align: center;
    position: relative;
}

.percentCount {
    position: absolute;
    font-weight: bold;
    top: 36%;
    left: 40%;
    transform: translate(0%, 0%);
}

.text {
    font-size: 1rem;
    font-weight: bolder;
}



#animateBar {
    transition: all 3s ease-in-out;
}

.circle-back {
    fill: none;
    stroke: var(--bgWhite);
}

.green {
    fill: none;
    stroke: #03661e;
    stroke-linecap: round;
}

.yellow {
    fill: none;
    stroke: #7a7a00;
    stroke-linecap: round;
}

.red {
    fill: none;
    stroke: #750404;
    stroke-linecap: round;
}

.pink {
    fill: none;
    stroke: #62067e;
    stroke-linecap: round;
}

.blue {
    fill: none;
    stroke: #033785;
    stroke-linecap: round;
}